.popup__title {
    margin: 0;
    padding: 0;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 1.2;
    text-align: left;
}

@media(min-width: 320px) and (max-width: 530px) {
    .popup__title {
        font-size: 18px;
        line-height: 1.22;
    }
}